.cuba {
    background: linear-gradient( to bottom, royalblue 20%, white 20%, white 40%, royalblue 40%, royalblue 60%, white 60%, white 80%, royalblue 80%, royalblue 80%);
}

.cuba .triangle {
    position: relative;
    width: 0;
    height: 0;
    border-left: 100px solid red;
    border-top: 75px solid transparent;
    border-bottom: 75px solid transparent;
}

.cuba .triangle .star-wrap {
    position: absolute;
    left: -160px;
    top: -85px;
    transform: scale(0.25);
}

.cuba .triangle .star-wrap .star {
    margin: 50px 0;
    position: relative;
    display: block;
    color: red;
    width: 0px;
    height: 0px;
    border-right: 100px solid transparent;
    border-bottom: 70px solid white;
    border-left: 100px solid transparent;
    transform: rotate(35deg);
}

.cuba .triangle .star-wrap .star::before {
    border-bottom: 80px solid white;
    border-left: 30px solid transparent;
    border-right: 30px solid transparent;
    position: absolute;
    height: 0;
    width: 0;
    top: -45px;
    left: -65px;
    display: block;
    content: "";
    transform: rotate(-35deg);
}

.cuba .triangle .star-wrap .star::after {
    position: absolute;
    display: block;
    color: red;
    top: 3px;
    left: -105px;
    width: 0px;
    height: 0px;
    border-right: 100px solid transparent;
    border-bottom: 70px solid white;
    border-left: 100px solid transparent;
    content: "";
    transform: rotate(-70deg);
}

